import React, {
  ChangeEvent,
  DetailedHTMLProps,
  InputHTMLAttributes,
} from "react";
// @ts-ignore
import styles from "./SuperCheckbox.module.scss";
//    
type DefaultInputPropsType = DetailedHTMLProps<
  InputHTMLAttributes<HTMLInputElement>,
  HTMLInputElement
>;

type SuperCheckboxPropsType = DefaultInputPropsType & {
  onChangeChecked?: (checked: boolean) => void;
  spanClassName?: string;
};

export const SuperCheckbox: React.FC<SuperCheckboxPropsType> = ({
  type, //          
  onChange,
  onChangeChecked,
  className,
  spanClassName,
  children, //     ,         React.FC

  ...restProps //       restProps
}) => {
  const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
    //     onChange  onChangeChecked
    onChange && onChange(e);
    onChangeChecked && onChangeChecked(e.currentTarget.checked);
  };

  const finalInputClassName = `${styles.checkbox} ${
    className ? className : ""
  }`;

  return (
    <label className={styles.label}>
      <input
        type={"checkbox"}
        onChange={onChangeCallback}
        className={finalInputClassName}
        {...restProps} //        (checked   )
      />
      {children && <span className={styles.spanClassName}>{children}</span>}
    </label> //  label      
  );
};
