import React, {
  ChangeEvent,
  DetailedHTMLProps,
  InputHTMLAttributes,
  KeyboardEvent,
} from "react";
// @ts-ignore
import styles from "./SuperInputText.module.scss";
//    
type DefaultInputPropsType = DetailedHTMLProps<
  InputHTMLAttributes<HTMLInputElement>,
  HTMLInputElement
>;

//               
// (   value: string, onChange: ...;      DefaultInputPropsType)
type SuperInputTextPropsType = DefaultInputPropsType & {
  //  +       
  onChangeText?: (value: string) => void;
  onEnter?: () => void;
  error?: string;
  spanClassName?: string;
};

export const SuperInputText: React.FC<SuperInputTextPropsType> = ({
  type, //          
  onChange,
  onChangeText,
  onKeyPress,
  onEnter,
  error,
  className,
  spanClassName,

  ...restProps //       restProps
}) => {
  const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
    onChange && //    onChange
      onChange(e); //     ( onChange  )

    onChangeText && onChangeText(e.currentTarget.value);
  };
  const onKeyPressCallback = (e: KeyboardEvent<HTMLInputElement>) => {
    onKeyPress && onKeyPress(e);

    onEnter && //    onEnter
      e.key === "Enter" && //     Enter
      onEnter(); //   
  };

  const finalSpanClassName = `${styles.error} ${
    spanClassName ? spanClassName : ""
  }`;
  const finalInputClassName = `${
    error ? styles.errorInput : styles.inputClass
  }`;

  return (
    <>
      <input
        type={type}
        // type={'text'}
        onChange={onChangeCallback}
        onKeyPress={onKeyPressCallback}
        className={finalInputClassName}
        {...restProps} //        (value   )
      />
      {error && <span className={finalSpanClassName}>{error}</span>}
    </>
  );
};
